ul {
  margin-block-start: 0; }
  ul li {
    list-style: none;
    min-width: 120px;
    padding: 1rem;
    background-color: #050609;
    border-bottom: 2px ridge #29324b;
    border-left: 2px dotted #29324b; }
    @media (min-width: 640px) {
      ul li {
        width: calc(100% / 3);
        float: left; } }
    ul li:first-of-type {
      border-left: 0px dotted black; }
    ul li a {
      font-family: 'Revalia', cursive;
      font-size: 1.5rem;
      color: #FFF;
      text-decoration: none; }
    ul li .active {
      color: #49b207; }
