code {
  font-family: 'VT323', monospace;
  font-family: 'Share Tech Mono', monospace;
  font-size: 1rem; }
  code strong {
    font-weight: 700; }

.dark {
  background-color: #1B1C1F;
  color: #49b207;
  -webkit-box-shadow: -3px 3px 3px 2px rgba(0, 0, 0, 0.85);
  -moz-box-shadow: -3px 3px 3px 2px rgba(0, 0, 0, 0.85);
  box-shadow: -3px 3px 3px 2px rgba(0, 0, 0, 0.85); }

.box {
  padding: 1rem;
  margin-top: 1.5rem;
  margin-left: 2.5rem;
  border: 2px outset #000;
  min-width: 200px;
  height: 190px;
  text-align: center; }
  @media (min-width: 960px) {
    .box {
      width: 200px;
      max-width: 200px; } }

i[class^="devicon"] {
  display: block;
  width: 4rem;
  margin: 0 auto 1.2rem;
  font-size: 4rem;
  color: #FFF; }
